@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.background{display:flex;justify-content:center;align-items:center;height:100vh;width:100vw;background-image:url("https://i.imgur.com/9bx5o5u.jpeg");background-color:#ccc;background-position:center;background-repeat:no-repeat;background-size:cover}.main{padding:calc(60px + 2vmin);display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;color:#343434;background-color:white;font-size:calc(14px + 1vmin);height:-webkit-max-content;height:-moz-max-content;height:max-content;width:-webkit-max-content;width:-moz-max-content;width:max-content;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}svg{width:calc(40px + 1vmin);padding:1vmin;fill:none;stroke:#b0b0b0;stroke-width:0.75px;stroke-linejoin:round}svg:hover{stroke:#2bc5ff}h1,h2,p{padding:calc(6px + 0.5vmin);margin:0;font-family:'Lato', 'Helvetica', sans-serif}

